#root {
  height: 100%;
    width: 100%;
}
.layout-wrapper {
  height: calc(100vh - 65px);
  width: 100%;

}

.lw-content-wrapper {
  display: flex;
  height: 100%;
}

.lw-header {
  background-color: #220D4E;
  height: 65px;
  width: 100%;
}

.lw-header-title {
  height: 100%;
  display: block;
  color: white;
  font-size: 3rem;
  margin: 0 10px;
}

.lw-content {
  width: 100%;
  height: 100%;


}

/* side-nav start  */
.lw-sidenav {
  width: 160px;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  padding-top: 20px;
  background: #FAFAFA;


}

.lw-sidenav a {
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  font-size: 25px;
  color: rgb(69, 90, 100);
  display: block;
}
/* side-nav end  */


.logo-image {
  height: 100%;
  width: 145px;
}
